@import '../../assets/sass/colors';

.header{
    width: 100%;
    padding-top: 20px; 
    text-align: center;

    img{
        border-radius:50%;
        padding: 5px;
        max-width: 250px;
        background-color: $darkColor;
    }
    h3{
       // text-align: left;
        padding: 0px;
        margin: 10px 0 0 0;
    }
    p{
       // text-align: left;
        line-height: 30px;
        color:$lightColor;
        font-size: 20px;
        margin: 0;
        a{
            text-decoration: none;
            font-size: 24px;
            font-weight: bold;
            color: lightblue;

        }
    }

}

.btn-ar {
    transition:all ease-in-out linear  1s !important;
    position: fixed;
    z-index: 1000;
    top: 5px;
    right: 10px;
    background-color: transparent;
    border: solid #fff 2px;
    color: #fff;
    border-radius: 5px;
    padding: 5px 10px;
    cursor: pointer;
    outline: none;
   
    &:hover{
        background-color:$darkColor;
    }
}
@media(max-width:578px){
    .header{
        p{
            padding: 0 0 0 5px !important;

        }
    }
}

.rtl{
    direction: rtl !important;
}

#en{
    text-align: left;
}
#ar{
    text-align: right;
}
.dis-none{
    text-align: right;
    display: none !important;
}